import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'status'
})
export class StatusPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    let humanStatus = "";

    switch (value) {
      case 0:
        humanStatus = "待发货";
        break;
      case 1:
        humanStatus = "待确认";
        break;
      case 2:
        humanStatus = "已完成";
        break;
      case 3:
        humanStatus = "已取消";
        break;

    }

    return humanStatus;
  }

}



