import { Component, NgZone } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { CouponService } from './coupon.service';
import { NotificationComponent } from './notification/notification.component';


@Component({
  providers: [NotificationComponent],
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  shipNum = 0;
  menuIndex = 1;

  constructor(
    private couponService: CouponService,
    private router: Router,
    private ngZone: NgZone,
    private notify: NotificationComponent) {

    this.router.events.subscribe(val => {
      if (val instanceof NavigationEnd) {
        let url = val.url;

        if (url.startsWith("/post")) {
          this.menuIndex = 2;
        } else if (url.startsWith("/mine")) {
          this.menuIndex = 3;
        } else if (url.startsWith("/help")) {
          this.menuIndex = 4;
        } else if (url.startsWith("/home")){
          this.menuIndex = 1;
          document.title = "星云优惠券-电子券C2C交易平台";
        } else if (url.startsWith("/coupon")){
          this.menuIndex = 0;
        } else if (url.startsWith("/")) {
          this.menuIndex = 1;
          document.title = "星云优惠券-电子券C2C交易平台";
        } else {
          this.menuIndex = 0;
        }
      }
    })

  }

  ngOnInit() {
    this.sellerGetAllOrders();
  }

  sellerGetAllOrders() {
    this.couponService.sellerGetAllOrders().subscribe(result => {
      this.ngZone.runOutsideAngular(() => {
        this.ngZone.run(() => {
          let r = JSON.parse(result);

          this.shipNum = r.countInfo.shipNum;
        });
      });

    }, error => {

    });
  }
}



