import { Pipe, PipeTransform } from '@angular/core';

import * as nebulas from 'nebulas';

@Pipe({
  name: 'weiToNas'
})
export class WeiToNasPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    let unit = nebulas.Unit;

    return unit.fromBasic(value, "nas");
  }

}



