import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { CouponService } from '../coupon.service';
import { NotificationComponent } from '../notification/notification.component';



declare const $: any;
declare const webExtensionWallet: any;

@Component({
  providers: [NotificationComponent],
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.css']
})
export class PostComponent implements OnInit {
  extProm = false;
  timer: any;
  categories = ["电子券", "满减券", "打车券", "骑行券", "外卖券", "电影券", "流量话费券", "其他"];
  selectedCategory = "电子券";

  constructor(private couponService: CouponService,
              private notify: NotificationComponent,
              private router: Router) {

  }

  ngOnInit() {

  }

  ngAfterViewInit() {
    let that = this;

    this.timer = setTimeout(function () {
      if (typeof(webExtensionWallet) === 'undefined') {
        that.extProm = true;
        window.clearTimeout(that.timer)
      }
    }, 1000);
  }

  onPostCoupon(postForm: NgForm, event) {
    let target = event.target;

    if (postForm.valid) {
      $(target).addClass("loading");

      this.couponService.addQuan(postForm.value).subscribe(value => {

      }, error => {
        this.notify.show(error);
        $(target).removeClass("loading");
      }, () => {
        this.notify.show("优惠券录入成功");
        $(target).removeClass("loading");
        this.router.navigate(["/mine"]);
      });
    }

  }

}



