import { Component, OnInit, NgZone} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CouponService } from '../coupon.service';
import { NotificationComponent } from '../notification/notification.component';



declare const $: any;

@Component({
  providers: [NotificationComponent],
  selector: 'app-mine',
  templateUrl: './mine.component.html',
  styleUrls: ['./mine.component.css']
})
export class MineComponent implements OnInit {
  allCoupon = [];
  buyerAllOrders = [];
  buyerCountInfo:any;
  sellerAllOrders = [];
  sellerCountInfo:any;
  tabIndex = 1;
  curAddr:any;

  constructor(private couponService: CouponService,
              private notify: NotificationComponent,
              private ngZone: NgZone,
              private aRoute: ActivatedRoute) {

  }

  ngOnInit() {
    this.sellerGetAllQuans();
    this.buyerGetAllOrders();
    this.sellerGetAllOrders();
    this.couponService.getCurAddr().subscribe(value => {
      this.curAddr = value;
    });
  }

  ngAfterViewInit() {
    let queryParams = this.aRoute.snapshot.queryParams;

    $('.tabular.menu .item').tab();

    if (queryParams['tabIndex']) {
      this.tabIndex = queryParams['tabIndex'];
    }
  }

  sellerGetAllQuans() {
    this.couponService.sellerGetAllQuans().subscribe(result => {
      this.ngZone.runOutsideAngular(() => {
        this.ngZone.run(() => {
          this.allCoupon = JSON.parse(result).reverse();
          console.log("出售的优惠券", this.allCoupon);
        });
      });

    }, error => {
      this.notify.show(error);
    });
  }

  buyerGetAllOrders() {
    this.couponService.buyerGetAllOrders().subscribe(result => {
      this.ngZone.runOutsideAngular(() => {
        this.ngZone.run(() => {
          let r = JSON.parse(result);

          this.buyerAllOrders = r.orders.reverse();
          this.buyerCountInfo = r.countInfo;
          console.log("买家统计信息", r.countInfo);
          console.log("买家所有订单", this.buyerAllOrders);
        });
      });

    }, error => {
      this.notify.show(error);
    });
  }

  sellerGetAllOrders() {
    this.couponService.sellerGetAllOrders().subscribe(result => {
      this.ngZone.runOutsideAngular(() => {
        this.ngZone.run(() => {
          let r = JSON.parse(result);

          this.sellerAllOrders = r.orders.reverse();
          this.sellerCountInfo = r.countInfo;

          if (r.countInfo.shipNum == 0) {
            $("#shipNum").hide();
          } else {
            $("#shipNum").text(r.countInfo.shipNum);
          }

          console.log("卖家统计信息", r.countInfo);
          console.log("卖家所有订单", this.sellerAllOrders);
        });
      });

    }, error => {
      this.notify.show(error);
    });
  }

  buyerCancelOrder(orderId, event) {
    let ele = $(event.target);

    ele.addClass("loading");
    this.couponService.buyerCancelOrder(orderId).subscribe(value => {

    }, error => {
      this.notify.show(error);
      ele.removeClass("loading");
    }, () => {
      this.notify.show("订单取消成功");
      this.buyerGetAllOrders();
      this.sellerGetAllOrders();
    });
  }

  shipOrder(orderId, email, event) {
    let ele = $(event.target);

    ele.addClass("loading");
    this.couponService.shipOrder(orderId, email).subscribe(value => {

    }, error => {
      this.notify.show(error);
      ele.removeClass("loading");
    }, ()=> {
      this.notify.show("订单已发货");
      this.buyerGetAllOrders();
      this.sellerGetAllOrders();
    });
  }

  receiveOrder(orderId, event) {
    let ele = $(event.target);

    ele.addClass("loading");
    this.couponService.receiveOrder(orderId).subscribe(value => {

    }, error => {
      ele.removeClass("loading");
      this.notify.show(error);
    }, ()=> {
      this.buyerGetAllOrders();
      this.sellerGetAllOrders();
    });
  }

  sellerWithDraw(event) {
    let ele = $(event.target);

    ele.addClass("loading");
    this.couponService.sellerWithDraw().subscribe(value => {
    }, error => {
      ele.removeClass("loading");
      this.notify.show(error);
    }, ()=> {
      this.sellerGetAllOrders();
      ele.removeClass("loading");
    });
  }

}



