import { Component, OnInit, ApplicationRef } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CouponService } from '../coupon.service';
import { NotificationComponent } from '../notification/notification.component';




declare const $:any;
declare const webExtensionWallet:any;


@Component({
  providers: [NotificationComponent],
  selector: 'app-coupon',
  templateUrl: './coupon.component.html',
  styleUrls: ['./coupon.component.css']
})
export class CouponComponent implements OnInit {
  maxError = false;
  timer:any;
  extProm = false;
  coupon: any;

  constructor(private couponService: CouponService,
              private ref: ApplicationRef,
              private notify: NotificationComponent,
              private aR: ActivatedRoute,
              private router: Router) { }

  ngOnInit() {
    this.couponService.getQuanItem(this.aR.snapshot.params["id"]).subscribe(val => {
      this.coupon = JSON.parse(val);
      document.title = '星云优惠券-' + this.coupon.name;
      this.ref.tick();
      $(".coupon.segment").removeClass("loading");
    });
  }

  ngAfterViewInit() {
    let that = this;

    this.timer = setTimeout(function () {
      if (typeof(webExtensionWallet) === 'undefined') {
        that.extProm = true;
        window.clearTimeout(that.timer)
      }
    }, 1000);
  }

  onBuyCoupon(postForm: NgForm, event, coupon) {
    let target = $(event.target);

    if (postForm.value.num > coupon.num) {
      return this.maxError = true;
    }
    if (postForm.valid) {
      target.addClass("loading");
      postForm.value.id = coupon.id;
      postForm.value.price = coupon.price;
      this.couponService.buyQuan(postForm.value).subscribe(value => {

      }, error => {
        this.notify.show(error);
        target.removeClass("loading");
      }, () => {
        this.notify.show("优惠券购买成功");
        target.removeClass("loading");
        this.router.navigate(["/mine"], {queryParams: {tabIndex: 2}});
      });
    }
  }
}



